@import url(https://fonts.googleapis.com/css?family=EB+Garamond:400,400i);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body,button{font-family:'EB Garamond', serif;font-size:1em;line-height:1.6;letter-spacing:.0625em;background-color:#fefefe}a,button{position:relative;display:inline-block;color:inherit;text-decoration:none}a.selected::after,a:hover::after,button.selected::after,button:hover::after{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:bottom left;transform-origin:bottom left}a::after,button::after{content:'';position:absolute;right:0;left:0;bottom:0;height:1px;background-color:#222;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:bottom right;transform-origin:bottom right;-webkit-transition:-webkit-transform .25s ease-out;transition:-webkit-transform .25s ease-out;transition:transform .25s ease-out;transition:transform .25s ease-out, -webkit-transform .25s ease-out}.is-inverted a::after,.is-inverted button::after{background:#fefefe}

